import React from "react";
import { Router, Route, Switch, withRouter } from "react-router";
import { Link } from "react-router-dom";
import { Provider } from "react-redux";

import store from "../redux/store";
import history from "../history";
import { logout } from "../actions/auth-actions";

import { theme } from "./theme";
import NavBar from "./NavBar";

import UserProfile from "./views/UserProfile";
import { HomeUnauth } from "./views/HomeUnauth";
import { Home } from "./views/Home";
import { Login } from "./views/Login";
import { Register } from "./views/Register";
import { Post } from "./views/Post";

import { PrivateRoute } from "./rerouting/PrivateRoute";
import { LogRegReroute } from "./rerouting/LogRegReroute";

import { ThemeProvider } from "@material-ui/styles";
import IconButton from "@material-ui/core/IconButton";
import { makeStyles } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";

import {
  faHome,
  faBars,
  faSignInAlt,
  faSignOutAlt,
  faSearchDollar
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const useStyles = makeStyles({
  list: {
    width: 250
  },
  fullList: {
    width: "auto"
  }
});

//COMPONENT START
function Dashboard() {
  const classes = useStyles();
  const [state, setState] = React.useState({
    left: false
  });

  let userInfo = store.getState().user.userInfo;
  const profile = `/profile/${userInfo._id}`;

  const toggleDrawer = (side, open) => event => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }
    setState({ ...state, [side]: open });
  };

  const handleLogout = () => {
    store.dispatch(logout());
  };

  const sideListUnauth = side => (
    <div
      className={classes.list}
      role="presentation"
      onClick={toggleDrawer(side, false)}
      onKeyDown={toggleDrawer(side, false)}
    >
      <List>
        <ListItem>
          <ListItemText
            primary={
              <h4>
                <b>codersWho</b>
              </h4>
            }
          />
        </ListItem>
      </List>
      <List>
        <Divider />
        <Link to="/" style={{ textDecoration: "none", color: "black" }}>
          <ListItem button>
            <ListItemIcon>
              <FontAwesomeIcon icon={faHome} />
            </ListItemIcon>
            <ListItemText primary={"Home"} />
          </ListItem>
        </Link>
        <Link to="/login" style={{ textDecoration: "none", color: "black" }}>
          <ListItem button>
            <ListItemIcon>
              <FontAwesomeIcon icon={faSignInAlt} />
            </ListItemIcon>
            <ListItemText
              style={{ textDecoration: "none" }}
              primary={"Sign In"}
            />
          </ListItem>
        </Link>
        <Divider />
      </List>
      <Divider />
    </div>
  );

  const sideListAuth = side => (
    <div
      className={classes.list}
      role="presentation"
      onClick={toggleDrawer(side, false)}
      onKeyDown={toggleDrawer(side, false)}
    >
      <List>
        <ListItem>
          <ListItemText
            primary={
              <h4
                className="mb-3 py-1 text-center border rounded"
                style={{ backgroundColor: "#7da2a9" }}
              >
                <b>codersWho</b>
              </h4>
            }
          />
        </ListItem>
        <ListItem>
          <div className="text-center mt-5" id="profileCardAvatarContainer">
            <img
              src="data:image/png;base64,"
              className="rounded-circle img-thumbnail w-50"
              alt="..."
            />
          </div>
        </ListItem>
        <ListItem className="justify-content-center py-0">
          <div>
            {`${store.getState().user.userInfo.firstName} ${
              store.getState().user.userInfo.lastName
            }`}
          </div>
        </ListItem>
      </List>
      <List>
        <Divider />
        <Link to="/home" style={{ textDecoration: "none", color: "black" }}>
          <ListItem button>
            <ListItemIcon>
              <FontAwesomeIcon icon={faHome} />
            </ListItemIcon>
            <ListItemText primary={"Home"} />
          </ListItem>
        </Link>
        <Link to={profile} style={{ textDecoration: "none", color: "black" }}>
          <ListItem button>
            <ListItemIcon>
              <FontAwesomeIcon icon={faSearchDollar} />
            </ListItemIcon>
            <ListItemText primary={"Profile"} />
          </ListItem>
        </Link>
        <ListItem button onClick={handleLogout}>
          <ListItemIcon>
            <FontAwesomeIcon icon={faSignOutAlt} />
          </ListItemIcon>
          <ListItemText primary={"Sign Out"} />
        </ListItem>
      </List>
      <Divider />
    </div>
  );

  const toggleButton = () => (
    <IconButton
      edge="start"
      className={classes.menuButton}
      color="inherit"
      aria-label="open drawer"
      onClick={toggleDrawer("left", true)}
    >
      <FontAwesomeIcon icon={faBars} />
    </IconButton>
  );

  return (
    <div>
      <ThemeProvider theme={theme}>
        <NavBar color="primary" btn={toggleButton} />
        <Drawer open={state.left} onClose={toggleDrawer("left", false)}>
          {store.getState().auth.isLoginSuccess
            ? sideListAuth("left")
            : sideListUnauth("left")}
        </Drawer>
        <Provider store={store}>
          <div className="mt-5 pt-3">
            <Router history={history}>
              <Switch>
                <Route exact path="/" component={HomeUnauth} />
                <LogRegReroute path="/login" component={Login} />
                <LogRegReroute path="/register" component={Register} />
                <PrivateRoute path="/home" component={Home} />
                <Route path="/profile/:userId" component={UserProfile} />
                <Route path="/post/:postId" component={Post} />
                <Route path="/*" component={() => "NOT FOUND"} />
              </Switch>
            </Router>
          </div>
        </Provider>
        {/* < NewsTicker /> */}
      </ThemeProvider>
    </div>
  );
}

export default withRouter(Dashboard);
